@import "variables";

body{
  .content{
    .container{
      .content__aside {
        grid-area: aside;
        width: 90%;
        div{
          background-color: @fontColor;
          a{
            padding-left: 15px;
          }
          h5{
            padding-left: 15px;
            height: 60px;
            font: 1.125em/60px @sansbold;
            color: #fff;
            &:hover{
              cursor: pointer;
            }
          }
          ul{
            height: 300px;
            overflow: auto;
            overflow-x: hidden;
            background-color: #77c9cc;
            li{
              padding: 5px 25px;
              a{
                padding-left: 0;
                font: 1em/1.125em @sansregular;
                color: @linkColor;
                text-decoration: none;
                &:hover{
                  text-decoration: underline;
                }
              }
              &:nth-child(3n+1){
                a{
                  font-family: @sansbold;
                }
              }
            }
          }
          .active{
            background-color: @linkColor;
          }
          &:first-child{
            border-top-left-radius: 10px;
            border-top-right-radius: 10px;
          }
          &:last-child{
            border-bottom-left-radius: 10px;
            border-bottom-right-radius: 10px;
          }
        }
        .sale{
          background-color: @hoverColor;

        }
        .sale,
        .catalog{
          a{
            font: 1.125em/60px @sansbold;
            color: #fff;
            text-decoration: none;
          }
        }
      }
    }
  }
}